___CSS_LOADER_EXPORT___.push([module.id, "/* \nTABLE OF CONTENTS\n\n  0. Layout\n  1. Sizes\n  2. Typography\n  3. Colors\n  4. Spacings\n  5. Top Navbar\n  6. Buttons\n\n*/\n/**\n * * Common Nav styles for mobile and desktop\n * * @since 3.0\n * * @importance core\n * * \n * * Table of Contents\n * *\n * *  1. Containers\n * *  2. Logo Image Link\n * *\n * **/\n#wmnav-cont {\n  z-index: 100;\n  top: 0;\n}\n\n#wmnav-content {\n  width: 100vw;\n  height: 100vh;\n  max-width: 100vw;\n  max-height: 100vh;\n  overflow: hidden;\n  display: flex;\n  flex-flow: column nowrap;\n}\n\n#wmnav-bar {\n  flex: 0 0 auto;\n  width: 100vw;\n  max-width: 100vw;\n  margin: 0;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  background: rgba(255, 255, 255, 0);\n  position: relative;\n  z-index: 1000;\n}\n\n#wmnav-logo {\n  width: 100px;\n  height: auto;\n  margin: calc(0.4rem + 0.25vw);\n  position: relative;\n}\n#wmnav-logo img {\n  width: 100%;\n  max-width: 100%;\n  height: auto;\n}\n\n/*!\n * Hamburgers\n * @description Tasty CSS-animated hamburgers\n * @author Jonathan Suh @jonsuh\n * @site https://jonsuh.com/hamburgers\n * @link https://github.com/jonsuh/hamburgers\n */\n.hamburger {\n  padding: 15px 15px;\n  display: inline-block;\n  cursor: pointer;\n  transition-property: opacity, filter;\n  transition-duration: 0.15s;\n  transition-timing-function: linear;\n  font: inherit;\n  color: inherit;\n  text-transform: none;\n  background-color: transparent;\n  border: 0;\n  margin: 0;\n  overflow: visible;\n}\n.hamburger:hover {\n  opacity: 0.7;\n}\n.hamburger.is-active:hover {\n  opacity: 0.7;\n}\n.hamburger.is-active .hamburger-inner,\n.hamburger.is-active .hamburger-inner::before,\n.hamburger.is-active .hamburger-inner::after {\n  background-color: gray;\n}\n\n.hamburger-box {\n  width: 40px;\n  height: 22px;\n  display: inline-block;\n  position: relative;\n}\n\n.hamburger-inner {\n  display: block;\n  top: 50%;\n  margin-top: -1px;\n}\n.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {\n  width: 40px;\n  height: 2px;\n  background-color: gray;\n  border-radius: 4px;\n  position: absolute;\n  transition-property: transform;\n  transition-duration: 0.15s;\n  transition-timing-function: ease;\n}\n.hamburger-inner::before, .hamburger-inner::after {\n  content: \"\";\n  display: block;\n}\n.hamburger-inner::before {\n  top: -10px;\n}\n.hamburger-inner::after {\n  bottom: -10px;\n}\n\n/*\n * Collapse\n */\n.hamburger--collapse .hamburger-inner {\n  top: auto;\n  bottom: 0;\n  transition-duration: 0.13s;\n  transition-delay: 0.13s;\n  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);\n}\n.hamburger--collapse .hamburger-inner::after {\n  top: -20px;\n  transition: top 0.2s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1), opacity 0.1s linear;\n}\n.hamburger--collapse .hamburger-inner::before {\n  transition: top 0.12s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1), transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);\n}\n.hamburger--collapse.is-active .hamburger-inner {\n  transform: translate3d(0, -10px, 0) rotate(-45deg);\n  transition-delay: 0.22s;\n  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);\n}\n.hamburger--collapse.is-active .hamburger-inner::after {\n  top: 0;\n  opacity: 0;\n  transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333), opacity 0.1s 0.22s linear;\n}\n.hamburger--collapse.is-active .hamburger-inner::before {\n  top: 0;\n  transform: rotate(-90deg);\n  transition: top 0.1s 0.16s cubic-bezier(0.33333, 0, 0.66667, 0.33333), transform 0.13s 0.25s cubic-bezier(0.215, 0.61, 0.355, 1);\n}\n\n#wm-burger {\n  z-index: 100;\n  position: relative;\n}\n\n#main-nav-toggler {\n  float: right;\n  margin-right: calc(0.8rem + 0.5vw);\n}\n\n#wmnav {\n  display: flex;\n  justify-content: space-between;\n  flex-wrap: nowrap;\n  position: fixed;\n  top: 0;\n  width: 100%;\n  background: white;\n  z-index: 1000;\n  border-bottom: 1px solid #BEBEBE;\n}\n\n#wmnav-slides {\n  width: 100vw;\n  height: 100vh;\n  max-width: 100vw;\n  max-height: 100vh;\n  background: white;\n  position: absolute;\n  top: 0;\n  left: 0;\n  display: none;\n}\n\n.wmnav-slide {\n  width: 100vw;\n  height: 100vh;\n  position: absolute;\n  display: none;\n  overflow: auto;\n}\n\n.wmnav-slide-inner {\n  display: flex;\n  min-height: 100vh;\n  flex-flow: column nowrap;\n  align-items: center;\n  justify-items: center;\n  align-content: center;\n  justify-content: center;\n}\n.wmnav-slide-inner h3 {\n  text-align: center;\n  padding: calc(0.4rem + 0.25vw);\n}\n\n.wmnav-subslide-head {\n  width: 100%;\n  flex: 0 1 auto;\n  margin-top: 6rem;\n  align-self: baseline;\n}\n.wmnav-subslide-head > h3 {\n  background: white;\n}\n\n.wmnav-subslide-body {\n  display: flex;\n  align-items: center;\n  flex: 1 1 auto;\n  padding-bottom: 6rem;\n}\n\n.wmnav-back-arrow-svg {\n  width: 120px;\n}\n\n.wmnav-back-arrow {\n  fill: none;\n  stroke: #787878;\n  stroke-width: 2;\n}\n\n.wmnav-back-button {\n  width: 150px;\n  height: auto;\n}", "",{"version":3,"sources":["webpack://./src/sass/components/_variables.scss","webpack://./src/nav/sass/components/_common_mobile_desktop.sass","webpack://./src/nav/sass/devices_nav.sass","webpack://./node_modules/hamburgers/_sass/hamburgers/hamburgers.scss","webpack://./node_modules/hamburgers/_sass/hamburgers/_base.scss","webpack://./src/nav/sass/components/_mobile_hamburger_settings.sass","webpack://./node_modules/hamburgers/_sass/hamburgers/types/_collapse.scss"],"names":[],"mappings":"AAAA;;;;;;;;;;;CAAA;ACCA;;;;;;;;;;KAAA;AAcA;EAEE,YAAA;EACA,MAAA;ACQF;;ADNA;EACE,YAAA;EACA,aAAA;EACA,gBAAA;EACA,iBAAA;EACA,gBAAA;EACA,aAAA;EACA,wBAAA;ACSF;;ADPA;EACE,cAAA;EACA,YAAA;EACA,gBAAA;EACA,SAAA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EAEA,kCAAA;EACA,kBAAA;EACA,aAAA;ACSF;;ADLA;EACE,YAAA;EACA,YAAA;EACA,6BDHa;ECIb,kBAAA;ACQF;ADNE;EACE,WAAA;EACA,eAAA;EACA,YAAA;ACQJ;;AC5DA;;;;;;EAAA;ACGA;EACE,kBAAA;EACA,qBAAA;EACA,eAAA;EAEA,oCAAA;EACA,0BAAA;EACA,k