 1200px;\r\n\r\n$screens: (\r\n  'sm': $small-screen,\r\n  'md': $medium-screen,\r\n  'lg': $large-screen\r\n);\r\n\r\n$base-margin : calc(0.8rem + 0.5vw);\r\n$double-margin : calc(1.6rem + 1vw);\r\n$quad-margin : calc(3.2rem + 2vw);\r\n$sexta-margin : calc(4.8rem + 3vw);\r\n$okto-margin : calc(6.4rem + 4vw);\r\n$dodeca-margin : calc(9.6rem + 6vw);\r\n\r\n$half-margin : calc(0.4rem + 0.25vw);\r\n$quarter-margin : calc(0.2rem + 0.15vw);\r\n$reduced-margin : 1rem;\r\n\r\n\r\n$-quarter-margin : calc((0.2rem + 0.15vw) * -1);\r\n$-half-margin: calc((0.4rem + 0.25vw) * -1);\r\n$-base-margin : calc((0.8rem + 0.5vw) * -1);\r\n$-double-margin : calc((0.8rem + 0.5vw) * -2);\r\n$-quad-margin : calc((0.8rem + 0.5vw) * -4);\r\n$-sexta-margin : calc((0.8rem + 0.5vw) * -6);\r\n$-okto-margin : calc((0.8rem + 0.5vw) * -8);\r\n\r\n\r\n// 2. Typography\r\n// ========================================================================== \r\n\r\n\r\n//$copy-font: 'Open Sans', Helvetica, Arial, sans-serif;\r\n$copy-font: sans-serif;\r\n$headline-font: 'Exo', sans-serif;\r\n\r\n$text-max-width: 720px;\r\n\r\n$headline-color: color('gray', 'darken-3');\r\n\r\n\r\n$bread-text-sm: 16px;\r\n$bread-text-md: 18px;\r\n$bread-text-lg: 18px;\r\n\r\n$bread-text-sm-line: 26px;\r\n$bread-text-md-line: 28px;\r\n$bread-text-lg-line: 30px;\r\n\r\n$tiny-text-sm: 10px;\r\n$tiny-text-md: 11px;\r\n$tiny-text-lg: 12px;\r\n\r\n$tiny-text-sm-line: 14px;\r\n$tiny-text-md-line: 16px;\r\n$tiny-text-lg-line: 18px;\r\n\r\n$small-text-sm: 11px;\r\n$small-text-md: 12px;\r\n$small-text-lg: 14px;\r\n\r\n$small-text-sm-line: 16px;\r\n$small-text-md-line: 17px;\r\n$small-text-lg-line: 20px;\r\n\r\n$large-text-sm: 18px;\r\n$large-text-md: 22px;\r\n$large-text-lg: 24px;\r\n\r\n$large-text-sm-line: 24px;\r\n$large-text-md-line: 28px;\r\n$large-text-lg-line: 32px;\r\n\r\n$huge-text-sm: 24px;\r\n$huge-text-md: 28px;\r\n$huge-text-lg: 34px;\r\n\r\n$huge-text-sm-line: 30px;\r\n$huge-text-md-line: 34px;\r\n$huge-text-lg-line: 40px;\r\n\r\n$mega-text-sm: 40px;\r\n$mega-text-md: 52px;\r\n$mega-text-lg: 64px;\r\n\r\n$mega-text-sm-line: 50px;\r\n$mega-text-md-line: 64px;\r\n$mega-text-lg-line: 76px;\r\n\r\n$h1 : calc(2.9rem + 1vw);\r\n$h1-h2 : calc(1.75rem + 1.1vw);\r\n$h2 : calc(1.95rem + 1vw);\r\n$h3 : calc(1.56rem + 0.8vw);\r\n$h4 : calc(1.32rem + 0.4vw);\r\n$h5 : calc(1.1rem + 0.1vw);\r\n$h6 : calc(0.7rem + 0.08vw);\r\n\r\n$h1-sm: 34px;\r\n$h1-md: 40px;\r\n$h1-lg: 48px;\r\n\r\n$h2-sm: 28px;\r\n$h2-md: 34px;\r\n$h2-lg: 40px;\r\n\r\n$h3-sm: 22px;\r\n$h3-md: 25px;\r\n$h3-lg: 29px;\r\n\r\n$h4-sm: 18px;\r\n$h4-md: 22px;\r\n$h4-lg: 25px;\r\n\r\n$h5-sm: $bread-text-sm;\r\n$h5-md: $bread-text-md;\r\n$h5-lg: $bread-text-lg;\r\n\r\n$h6-sm: 15px;\r\n$h6-md: 16px;\r\n$h6-lg: 17px;\r\n\r\n\r\n$bread-text-sm: 16px;\r\n$bread-text-md: 18px;\r\n$bread-text-lg: 18px;\r\n\r\n$bread-text-sm-line: 26px;\r\n$bread-text-md-line: 28px;\r\n$bread-text-lg-line: 30px;\r\n\r\n$tiny-text-sm: 10px;\r\n$tiny-text-md: 11px;\r\n$tiny-text-lg: 12px;\r\n\r\n$tiny-text-sm-line: 14px;\r\n$tiny-text-md-line: 16px;\r\n$tiny-text-lg-line: 18px;\r\n\r\n$small-text-sm: 11px;\r\n$small-text-md: 12px;\r\n$small-text-lg: 14px;\r\n\r\n$small-text-sm-line: 16px;\r\n$small-text-md-line: 17px;\r\n$small-text-lg-line: 20px;\r\n\r\n\r\n$big-text : calc(2.5rem + 0.7vw);\r\n$bigger-text : calc(2.9rem + 0.9vw);\r\n$biggest-text : calc(3.2rem + 1.2vw);\r\n\r\n$h4-lineheight : calc(1.7rem + 0.2vw);\r\n$h4-padding : calc(1.7rem + 0.2vw) 0;\r\n\r\n$tiny-icon : calc(1.8rem + 0.4vw);\r\n$small-icon : calc(2rem + 0.5vw);\r\n$medium-icon : calc(2.6rem + 0.8vw);\r\n$big-icon : calc(4rem + 1vw);\r\n$huge-icon : calc(6rem + 1.6vw);\r\n$mega-icon: calc(8rem + 2vw);\r\n\r\n\r\n$mega-font : calc(2rem + 2vw);\r\n\r\n$lg-button: calc(4rem + 2vw);\r\n$md-button: calc(2rem + 1vw);\r\n$sm-button: calc(1rem + 0.5vw);\r\n\r\n// 3. Colors\r\n// ==========================================================================\r\n\r\n$primary-color : color(\"essblue\", \"base\");\r\n$primary-color-hover : color(\"essblue\", \"darken-3\");\r\n$primary-text : color(\"gray\", \"darken-2\");\r\n$secondary-color : color(\"gray\", \"darken-2\");\r\n$white : color(\"shades\", \"white\");\r\n\r\n//SYSTEM\r\n$success-color : color(\"green\", \"base\");\r\n$error-color : color(\"red\", \"base\");\r\n\r\n$link-color : color(\"gray\", \"base\");\r\n$ess-base-green : color(\"green\", \"base\");\r\n\r\n$card-link-color : color(\"essblue\", \"base\");\r\n$footer-font-color : color(\"gray\", \"base\");\r\n$footer-bg-color : color(\"gray\", \"lighten-4\");\r\n$footer-copyright-font-color: color(\"gray\", \"lighten-2\");\r\n$footer-copyright-bg-color : none;\r\n$ess-blue-transp : rgba(15, 42, 86, 0.2);\r\n$ess-blue-transp-hard : rgba(15, 42, 86, 0.8);\r\n\r\n\r\n\r\n\r\n// GRADIENT\r\n$ess-blue-gradient : linear-gradient(to left, color('gray', 'base'), color('gray', 'lighten-1'));\r\n$ess-blue-gradient-reverse : linear-gradient(to right, $primary-color, $secondary-color);\r\n\r\n\r\n// button colors \r\n$primary-button-bg : $primary-color;\r\n$primary-button-bg-hover : color('essblue', 'darken-1');\r\n\r\n// NAVIGATION CUBE\r\n// Current one\r\n//$nav-cube-background: radial-gradient(circle, rgba(230, 230, 230, 1) 0%, rgba(211, 211, 211, 0.85) 77%);\r\n\r\n// New one\r\n$nav-cube-background: rgba(12, 60, 146, 0.35);\r\n\r\n\r\n\r\n// 4. Spacings and Sizes\r\n// ==========================================================================\r\n\r\n\r\n$spaces-map: (\r\n  \"zero\" : 0,\r\n  \"quarter\": $quarter-margin,\r\n  \"half\": $half-margin,\r\n  \"base\" : $base-margin,\r\n  \"double\" : $double-margin,\r\n  \"quad\" : $quad-margin,\r\n  \"sexta\": $sexta-margin,\r\n  \"okta\" : $okto-margin,\r\n  \"dodeca\" : $dodeca-margin,\r\n  \"minus-half\": $-half-margin,\r\n  \"minus-base\": $-base-margin,\r\n  \"minus-double\": $-double-margin,\r\n  \"minus-quad\": $-quad-margin,\r\n  \"minus-okta\": $-okto-margin,\r\n);\r\n\r\n$spaces-values: (\r\n  \"m\": (\"margin\"),\r\n  \"m-top\": (\"margin-top\"),\r\n  \"m-right\": (\"margin-right\"),\r\n  \"m-bot\": (\"margin-bottom\"),\r\n  \"m-left\": (\"margin-left\"),\r\n  \"m-hor\": (\"margin-left\", \"margin-right\"),\r\n  \"m-vert\": (\"margin-top\", \"margin-bottom\"),\r\n  \"p\":(\"padding\"),\r\n  \"p-top\": (\"padding-top\"),\r\n  \"p-right\": (\"padding-right\"),\r\n  \"p-bot\": (\"padding-bottom\"),\r\n  \"p-left\": (\"padding-left\"),\r\n  \"p-hor\": (\"padding-left\", \"padding-right\"),\r\n  \"p-vert\": (\"padding-top\", \"padding-bottom\"),\r\n);\r\n\r\n$sizes: (\r\n  \"full\": 100%,\r\n  \"auto\": auto,\r\n  \"50\": 50px,\r\n  \"100\": 100px,\r\n  \"150\": 150px,\r\n  \"200\": 200px,\r\n  \"300\": 300px,\r\n  \"400\": 400px,\r\n  \"600\": 600px,\r\n  \"700\": 700px,\r\n  \"800\": 800px,\r\n  \"1000\": 1000px,\r\n  \"1200\": 1200px,\r\n  \"1400\": 1400px,\r\n);\r\n\r\n$offsets: (\r\n  \"minus-600\": -600px,\r\n  \"minus-400\": -400px,\r\n  \"minus-300\": -300px,\r\n  \"minus-200\": -200px,\r\n  \"minus-150\": -150px,\r\n  \"minus-100\": -100px,\r\n  \"minus-50\": -50px,\r\n  \"minus-25\": -25px,\r\n  \"0\": 0px,\r\n  \"25\": 25px,\r\n  \"50\": 50px,\r\n  \"100\": 100px,\r\n  \"150\": 150px,\r\n  \"200\": 200px,\r\n  \"300\": 300px,\r\n  \"400\": 400px,\r\n  \"600\": 600px,\r\n  \"800\": 800px,\r\n  \"1000\": 800px,\r\n  \"1200\": 800px,\r\n  \"1400\": 800px\r\n);\r\n\r\n$opacities: (\r\n  \"0\": 0,\r\n  \"10\": 0.1,\r\n  \"20\": 0.2,\r\n  \"30\": 0.3,\r\n  \"40\": 0.4,\r\n  \"50\": 0.5,\r\n  \"60\": 0.6,\r\n  \"70\": 0.7,\r\n  \"80\": 0.8,\r\n  \"90\": 0.9,\r\n  \"100\":1\r\n);\r\n\r\n\r\n// 5. Top Navbar\r\n// ==========================================================================\r\n\r\n$topnav_padding: $base-margin;\r\n\r\n$nav_cube_side_width: 1.4rem;\r\n$nav_cube_side_shift: 0.9rem;\r\n$nav_cube_side_shift_2: 1rem;\r\n\r\n\r\n// 6. Blocks\r\n// ==========================================================================\r\n\r\n$block-border-radius: 10px;\r\n\r\n// 6. Buttons\r\n// ==========================================================================\r\n\r\n$button-primary: color('essblue', 'darken-1');\r\n$button-secondary: color('essblue', 'darken-2');\r\n$button-text: white;\r\n$button-text-hover: white","\n/**\n * Common Nav styles for mobile and desktop\n * @since 3.0\n * @importance core\n * \n * Table of Contents\n *\n *  1. Containers\n *  2. Logo Image Link\n *\n **/\n\n// 1. TOP NAVBAR CONTAINER LAYOUT ----------------------------------------------------\n\n#wmnav-cont\n  //position: fixed\n  z-index: 100\n  top: 0\n\n#wmnav-content\n  width: 100vw\n  height: 100vh\n  max-width: 100vw\n  max-height: 100vh\n  overflow: hidden\n  display: flex\n  flex-flow: column nowrap\n\n#wmnav-bar\n  flex: 0 0 auto\n  width: 100vw\n  max-width: 100vw\n  margin: 0\n  display: flex\n  justify-content: space-between\n  align-items: center\n  //border-bottom: 1px solid color(\"gray\", \"base\")\n  background: rgba(255,255,255,0)\n  position: relative\n  z-index: 1000\n\n// 2. TOP NAVBAR LOGO ----------------------------------------------------\n\n#wmnav-logo\n  width: 100px\n  height: auto\n  margin: $half-margin\n  position: relative\n\n  img\n    width: 100%\n    max-width: 100%\n    height: auto\n","@import '../../sass/components/variables'\n@import '../../sass/components/colors'\n@import './components/common_mobile_desktop'\n\n@import 'components/mobile_hamburger_settings'\n@import 'hamburgers/_sass/hamburgers/hamburgers'\n\n#wm-burger\n  z-index: 100\n  position: relative\n\n#main-nav-toggler\n  float: right\n  margin-right: $base-margin\n\n#wmnav\n  display: flex\n  justify-content: space-between\n  flex-wrap: nowrap\n  position: fixed\n  top: 0\n  width: 100%\n  background: white\n  z-index: 1000\n  border-bottom: 1px solid color('gray', 'base')\n\n#wmnav-slides\n  width: 100vw\n  height: 100vh\n  max-width: 100vw\n  max-height: 100vh\n  background: white\n  position: absolute\n  top: 0\n  left: 0\n  display: none\n\n.wmnav-slide\n  width: 100vw\n  height: 100vh\n  position: absolute\n  display: none\n  overflow: auto\n\n.wmnav-slide-inner\n  display: flex\n  min-height: 100vh\n  flex-flow: column nowrap\n  align-items: center\n  justify-items: center\n  align-content: center\n  justify-content: center\n\n  h3\n    text-align: center\n    padding: $half-margin\n\n.wmnav-subslide-head\n  width: 100%\n  flex: 0 1 auto // grow shrink basis\n  margin-top: 6rem\n  align-self: baseline\n\n  &>h3\n    background: white\n\n.wmnav-subslide-body\n  display: flex\n  align-items: center\n  flex: 1 1 auto\n  padding-bottom: 6rem\n\n.wmnav-back-arrow-svg\n  width: 120px\n\n.wmnav-back-arrow\n  fill: none\n  stroke: color('gray', 'darken-1')\n  stroke-width: 2\n\n.wmnav-back-button\n  width: 150px\n  height: auto\n","@charset \"UTF-8\";\n/*!\n * Hamburgers\n * @description Tasty CSS-animated hamburgers\n * @author Jonathan Suh @jonsuh\n * @site https://jonsuh.com/hamburgers\n * @link https://github.com/jonsuh/hamburgers\n */\n\n// Settings\n// ==================================================\n$hamburger-padding-x           : 15px !default;\n$hamburger-padding-y           : 15px !default;\n$hamburger-layer-width         : 40px !default;\n$hamburger-layer-height        : 4px !default;\n$hamburger-layer-spacing       : 6px !default;\n$hamburger-layer-color         : #000 !default;\n$hamburger-layer-border-radius : 4px !default;\n$hamburger-hover-opacity       : 0.7 !default;\n$hamburger-active-layer-color  : $hamburger-layer-color !default;\n$hamburger-active-hover-opacity: $hamburger-hover-opacity !default;\n\n// To use CSS filters as the hover effect instead of opacity,\n// set $hamburger-hover-use-filter as true and\n// change the value of $hamburger-hover-filter accordingly.\n$hamburger-hover-use-filter   : false !default;\n$hamburger-hover-filter       : opacity(50%) !default;\n$hamburger-active-hover-filter: $hamburger-hover-filter !default;\n\n// Types (Remove or comment out what you dont need)\n// ==================================================\n$hamburger-types: (\n  3dx,\n  3dx-r,\n  3dy,\n  3dy-r,\n  3dxy,\n  3dxy-r,\n  arrow,\n  arrow-r,\n  arrowalt,\n  arrowalt-r,\n  arrowturn,\n  arrowturn-r,\n  boring,\n  collapse,\n  collapse-r,\n  elastic,\n  elastic-r,\n  emphatic,\n  emphatic-r,\n  minus,\n  slider,\n  slider-r,\n  spin,\n  spin-r,\n  spring,\n  spring-r,\n  stand,\n  stand-r,\n  squeeze,\n  vortex,\n  vortex-r\n) !default;\n\n// Base Hamburger (We need this)\n// ==================================================\n@import \"base\";\n\n// Hamburger types\n// ==================================================\n@import \"types/3dx\";\n@import \"types/3dx-r\";\n@import \"types/3dy\";\n@import \"types/3dy-r\";\n@import \"types/3dxy\";\n@import \"types/3dxy-r\";\n@import \"types/arrow\";\n@import \"types/arrow-r\";\n@import \"types/arrowalt\";\n@import \"types/arrowalt-r\";\n@import \"types/arrowturn\";\n@import \"types/arrowturn-r\";\n@import \"types/boring\";\n@import \"types/collapse\";\n@import \"types/collapse-r\";\n@import \"types/elastic\";\n@import \"types/elastic-r\";\n@import \"types/emphatic\";\n@import \"types/emphatic-r\";\n@import \"types/minus\";\n@import \"types/slider\";\n@import \"types/slider-r\";\n@import \"types/spin\";\n@import \"types/spin-r\";\n@import \"types/spring\";\n@import \"types/spring-r\";\n@import \"types/stand\";\n@import \"types/stand-r\";\n@import \"types/squeeze\";\n@import \"types/vortex\";\n@import \"types/vortex-r\";\n\n// ==================================================\n// Cooking up additional types:\n//\n// The Sass for each hamburger type should be nested\n// inside an @if directive to check whether or not\n// it exists in $hamburger-types so only the CSS for\n// included types are generated.\n//\n// e.g. hamburgers/types/_new-type.scss\n//\n// @if index($hamburger-types, new-type) {\n//   .hamburger--new-type {\n//     ...\n//   }\n// }\n","@use \"sass:math\";\n\n// Hamburger\n// ==================================================\n.hamburger {\n  padding: $hamburger-padding-y $hamburger-padding-x;\n  display: inline-block;\n  cursor: pointer;\n\n  transition-property: opacity, filter;\n  transition-duration: 0.15s;\n  transition-timing-function: linear;\n\n  // Normalize (<button>)\n  font: inherit;\n  color: inherit;\n  text-transform: none;\n  background-color: transparent;\n  border: 0;\n  margin: 0;\n  overflow: visible;\n\n  &:hover {\n    @if $hamburger-hover-use-filter == true {\n      filter: $hamburger-hover-filter;\n    }\n    @else {\n      opacity: $hamburger-hover-opacity;\n    }\n  }\n\n  &.is-active {\n    &:hover {\n      @if $hamburger-hover-use-filter == true {\n        filter: $hamburger-active-hover-filter;\n      }\n      @else {\n        opacity: $hamburger-active-hover-opacity;\n      }\n    }\n\n    .hamburger-inner,\n    .hamburger-inner::before,\n    .hamburger-inner::after {\n      background-color: $hamburger-active-layer-color;\n    }\n  }\n}\n\n.hamburger-box {\n  width: $hamburger-layer-width;\n  height: $hamburger-layer-height * 3 + $hamburger-layer-spacing * 2;\n  display: inline-block;\n  position: relative;\n}\n\n.hamburger-inner {\n  display: block;\n  top: 50%;\n  margin-top: math.div($hamburger-layer-height, -2);\n\n  &,\n  &::before,\n  &::after {\n    width: $hamburger-layer-width;\n    height: $hamburger-layer-height;\n    background-color: $hamburger-layer-color;\n    border-radius: $hamburger-layer-border-radius;\n    position: absolute;\n    transition-property: transform;\n    transition-duration: 0.15s;\n    transition-timing-function: ease;\n  }\n\n  &::before,\n  &::after {\n    content: \"\";\n    display: block;\n  }\n\n  &::before {\n    top: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;\n  }\n\n  &::after {\n    bottom: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;\n  }\n}\n","\n$hamburger-layer-height: 2px\n$hamburger-layer-spacing: 8px\n$hamburger-layer-color: gray\n\n$hamburger-types: ( collapse )\n","@if index($hamburger-types, collapse) {\n  /*\n   * Collapse\n   */\n  .hamburger--collapse {\n    .hamburger-inner {\n      top: auto;\n      bottom: 0;\n      transition-duration: 0.13s;\n      transition-delay: 0.13s;\n      transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);\n\n      &::after {\n        top: ($hamburger-layer-spacing * 2 + $hamburger-layer-height * 2) * -1;\n        transition: top 0.2s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1),\n                    opacity 0.1s linear;\n      }\n\n      &::before {\n        transition: top 0.12s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1),\n                    transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);\n      }\n    }\n\n    &.is-active {\n      .hamburger-inner {\n        transform: translate3d(0, ($hamburger-layer-spacing + $hamburger-layer-height) * -1, 0) rotate(-45deg);\n        transition-delay: 0.22s;\n        transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);\n\n        &::after {\n          top: 0;\n          opacity: 0;\n          transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333),\n                      opacity 0.1s 0.22s linear;\n        }\n\n        &::before {\n          top: 0;\n          transform: rotate(-90deg);\n          transition: top 0.1s 0.16s cubic-bezier(0.33333, 0, 0.66667, 0.33333),\n                      transform 0.13s 0.25s cubic-bezier(0.215, 0.61, 0.355, 1);\n        }\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
